body{
  font-size: 5vw;
  padding:2vw 2vw 4vw;
  margin:0;
}

table{
  border-collapse: collapse;
  margin:0 auto 4vw;
  padding:0;
}

input{
  font-size: 5vw;
}

td{
  padding:5px;
}

.button{
  font-size: 4vw;
  width: max-content;
  margin: 0 auto;
  padding:1vw 3vw;
  color: #fff;
  background-color: #1677ff;
  box-shadow: 0 2px 0 rgb(5 145 255 / 10%);
  border-radius: 6px;
}